footer {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin-top: 20px;
  }
  
  footer a {
    text-decoration: none;
    color: white;
    transition: transform 0.3s;
  }
  
  footer a:hover {
    transform: scale(1.2);
    color: #ff6b6b;
  }
  